import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { NewService } from '../../angular-services/new.service';
import { NewCategoryService } from '../../angular-services/new-category.service';
import { NewCategory } from '../../classes/new-category';
import { New } from '../../classes/new';

@Component({
  selector: 'app-edit-news-form-admin',
  templateUrl: './edit-news-form.component.html',
  styleUrls: ['./edit-news-form.component.css']
})
export class EditNewsFormComponent implements OnInit {

  @Output() newsTitle = new EventEmitter<string>();
  formEditNew: FormGroup;
  alert: any = { type: '', message: '', show: false };
  categories: NewCategory[] = [];
  news: New = new New();
  mediaType = 'news';
  urlObjects: string = 'https://viruscursinho.com.br:3002/news';
  showModalSelectFile: boolean = false;

  constructor(private newService: NewService, private categoryService: NewCategoryService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getCategories();
    this.getCourse();
  }

  initForm() {
    this.formEditNew = new FormGroup({
      title: new FormControl('', Validators.required),
      content: new FormControl('', Validators.required),
      photo: new FormControl(''),
      idCategory: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  getCourse() {
    let key = this.route.snapshot.paramMap.get('id');
    this.newService.getNew(key).subscribe(
      response => {
        if (response !== undefined) {
          this.news = response.news;
          this.news.idCategory = response.category._key;
          this.newsTitle.emit(this.news.title);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditNew.patchValue({
      title: this.news.title,
      content: this.news.content,
      idCategory: this.news.idCategory,
      photo: this.news.photo,
      status: this.news.status+''
    });
  }

  save() {
    if (this.formEditNew.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditNew.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.newService.updateNew(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  getCategories() {
    this.categoryService.getCategories().subscribe(
      response => {
        if (response !== undefined) {
          for (let category of response) {
            if (category.status) {
              this.categories.push(category);
            }
          }
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formEditNew.patchValue({
      photo: link
    });
    this.showModalSelectFile = false;
  }

}
